<template>
  <v-container fluid fill-height>
    <v-layout align-center justify-center>
      <v-flex xs12 sm8 md4>
        <v-card>
            <v-alert :value="info" type="info" class="alert-info">{{info}}</v-alert>
        </v-card>
        <v-card class="elevation-12 signForm--hide" id="signForm">
          <v-toolbar dark color="primary">
            <v-toolbar-title>Вход</v-toolbar-title>
            <v-spacer></v-spacer>
          </v-toolbar>
          <v-card-text>
            <v-alert :value="error" type="error">{{error}}</v-alert>
            <v-alert :value="success" type="success">{{success}}</v-alert>
            <v-form v-model="valid">
              <v-text-field
                prepend-icon="person"
                name="name"
                label="Имя"
                type="text"
                required
                v-model="name"
                :rules="nameRules"
              ></v-text-field>
              <v-text-field
                id="password"
                prepend-icon="lock"
                name="password"
                label="Пароль"
                type="password"
                required
                v-model="password"
                :rules="passwordRules"
                :loading="loading"
              ></v-text-field>
            </v-form>
          </v-card-text>
          <v-card-actions>
            <v-spacer></v-spacer>
            <v-btn color="primary" @click.prevent="signin" :disabled="processing || !valid">Войти</v-btn>
          </v-card-actions>
        </v-card>
      </v-flex>
    </v-layout>
  </v-container>
</template>

<script>
export default {
  data() {
    return {
      name: null,
      password: null,
      valid: false,
      loading: false,
      nameRules: [v => !!v || "Пожалуйста введите Ваше имя"],
      passwordRules: [
        v => !!v || "Пожалуйста введите пароль",
        v =>
          (v && v.length >= 4) || "Пароль слишком короткий - минимум 4 символов"
      ]
    };
  },
  computed: {
    success() {
      return this.$store.getters.getSuccess;
    },
    error() {
      return this.$store.getters.getError;
    },
    info() {
      return this.$store.getters.getInfo;
    },
    processing() {
      return this.$store.getters.getProcessing;
    },
    isUserAuthenticated() {
      return this.$store.getters.isUserAuthenticated;
    }
  },
  watch: {
    isUserAuthenticated(val) {
      if (val === true) this.$router.push("/");
    }
  },
  methods: {
    signin() {
      this.$store.dispatch("SIGNIN", {
        name: this.name,
        password: this.password
      });
    }
  }
};
</script>