<template>
  <div class="card vld-parent mb-3" id="preloader">
    <loading    
            :active.sync="isLoading"
             :can-cancel="canCancel"
             :on-cancel="whenCancelled"
             :is-full-page="fullPage"
             :height="height"
             :width="width"
             :color="color"
             :loader="loader"
             :background-color="bgColor">
      <h3 v-if="useSlot">Loading ...</h3>
    </loading>
  </div>
</template>

<script>
import Vue from 'vue'
import Loading from 'vue-loading-overlay'
import 'vue-loading-overlay/dist/vue-loading.css'
import { setTimeout } from 'timers';
// import { mutationObserver } from '../store/triggerClassOfDom.js'
Vue.use(Loading)

export default {
    data() {
      return {
        isLoading: false,
        fullPage: true,
        canCancel: false,
        useSlot: false,
        loader: 'spinner',
        color: '#007bff',
        bgColor: '#ffffff',
        height: 64,
        width: 64,
        timeout: 3000, //ms
      }
    },
    components: {
      Loading
    },
    methods: {
      simulate() {
        this.isLoading = true;
        // Simulate async call
        setTimeout(() => {
          this.isLoading = false
        }, this.timeout);
      },
      whenCancelled() {
        console.log("User cancelled the loader.")
      }
    },
    mounted () {
        this.simulate();

        document.body.onload = function()
          {
              setTimeout(function() 
              {
                  var formSignIn =  document.querySelector('#signForm')
                  if (this.isLoading = true){
                      formSignIn.classList.remove('signForm--hide')
                  }
              }, 3000);
          }
    }
  }
</script>

<style>
  .signForm--hide {
    opacity: 0;
    transition: opacity 1000ms ease;
  }
</style>

