export default {
  actions: {
    SIGNIN({ commit }, payload) {
      commit("SET_PROCESSING", true);
      commit("CLEAR_ERROR");
      
      var name = document.querySelector('input[name=name]').value;
      var password = document.querySelector('input[name=password]').value;
      var formSingIn = document.querySelector('#signForm');

      if (name == 'test' && password == 'test') {
        commit('SET_SUCCESS', 'Успешная авторизация')
        setTimeout(() => {
          formSingIn.classList.add('signForm--hide')
        }, 3000);
        setTimeout(() => {
          document.querySelector('.alert-info').style.display = 'block'
          document.querySelector('.alert-info').textContent = 'Добро пожаловать'
        }, 4000);
      } else {
        commit('SET_ERROR', 'Ошибка авторизации')
      }
      // firebase
      //   .auth()
      //   .signInWithEmailAndPassword(payload.email, payload.password)
      //   .then(() => {
      //     commit("SET_PROCESSING", false);
      //   })
      //   .catch(function(error) {
      //     commit("SET_PROCESSING", false);
      //     commit("SET_ERROR", error.message);
      //   });
    }
  }
};
